 import {  Link, useRouteMatch } from 'react-router-dom';
export default function LoadFilmList({ films }) { 
    const match = useRouteMatch();

    return (
        <>
            <ul>
                {films.map(item => (
                    <li key={item.id}>
                        <Link to={`movies/${item.id}`}> { item.title} </Link>
                    </li>
                ))}
            </ul>    
                
        </>    
    )
}